/* Homepage Container */
.homepage-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    transition: background-color 0.3s, color 0.3s;
}

/* Header */
.header {
    text-align: center;
    margin-bottom: 30px;
    font-size: 3.5rem;
    font-family: 'Playfair Display', serif;
    color: #333333;
}

/* Navbar */
.navbar {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin-bottom: 30px;
}

.nav-link {
    padding: 10px 20px;
    background-color: #ffffff;
    border: 2px solid #007bff;
    color: #007bff;
    font-size: 1.1rem;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.nav-link.active {
    background-color: #007bff;
    color: #ffffff;
}

.nav-link:hover {
    background-color: #e0e0e0;
    border-color: #0056b3;
}

/* News Grid */
.news-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 30px;
}

/* News Article */
.news-article {
    background-color: #ffffff;
    padding: 20px;
    border-radius: 15px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.news-article:hover {
    transform: translateY(-8px);
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
}

/* Article Image */
.article-image {
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-radius: 12px;
}

.placeholder-image {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 200px;
    background-color: #ccc;
    border-radius: 12px;
    font-size: 1.2rem;
    color: #fff;
}

/* Article Text */
.article-title {
    font-size: 1.75rem;
    font-weight: 600;
    margin: 20px 0 10px;
    color: #333333;
}

.article-description {
    font-size: 1.1rem;
    line-height: 1.6;
    color: #555555;
    margin-bottom: 15px;
}

/* Read More Button */
.read-more {
    display: inline-block;
    padding: 10px 20px;
    background-color: #007bff;
    color: #ffffff;
    text-decoration: none;
    border-radius: 5px;
    font-weight: 500;
    transition: background-color 0.3s ease;
}

.read-more:hover {
    background-color: #0056b3;
}

/* Loading and Error Text */
.loading, .error {
    text-align: center;
    font-size: 1.2rem;
    color: #ff3333;
}

.profile-dropdown {
    position: absolute;
    top: 20px; /* Adjust as needed for vertical positioning */
    right: 10px; /* Increase this value to move the icon further to the right */
}

.user-icon {
    cursor: pointer;
    font-size: 40px;
    color: inherit;
}


.dropdown-menu {
    position: absolute;
    top: 50px;
    right: 0;
    background-color: #ffffff;
    border: 1px solid #ccc;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    z-index: 1000;
    padding: 10px;
}

.dropdown-menu button {
    display: block;
    width: 100%;
    text-align: left;
    padding: 10px;
    background: none;
    border: none;
    color: #333;
    cursor: pointer;
}

.dropdown-menu button:hover {
    background-color: #f0f0f0;
}

/* Dark Mode */
body.dark-mode {
    background-color: #121212;
    color: #ffffff;
}

body.dark-mode .header {
    color: #ffffff;
}

body.dark-mode .navbar, .search-form, .source-dropdown-container, .news-grid {
    background-color: #1e1e1e;
}

body.dark-mode .nav-link {
    background-color: #333;
    color: #ffffff;
    border: 2px solid #555;
}

body.dark-mode .nav-link.active {
    background-color: #555;
}

body.dark-mode .loading, .error {
    color: #ffcc00;
}

body.dark-mode .dropdown-menu {
    background-color: #333;
}

body.dark-mode .dropdown-menu button {
    color: #ffffff;
}

body.dark-mode .dropdown-menu button:hover {
    background-color: #444;
}

/* Weather Section */
.weather-section {
    background-color: #e3f2fd;
    border-radius: 15px;
    padding: 25px;
    margin: 25px 0;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
    text-align: center;
    width: 30%;
}

.weather-section h2 {
    font-size: 24px;
    color: #007bff;
    margin-bottom: 15px;
}

.weather-section p {
    font-size: 18px;
    color: #555;
    margin: 8px 0;
}

.weather-section p:last-child {
    font-weight: bold;
}

/* Language Selector */
.language-selector {
    margin: 20px 0;
    display: flex;
    align-items: center;
}

.language-selector label {
    margin-right: 10px;
    font-size: 1rem;
    color: #555;
}

.language-selector select {
    padding: 8px;
    font-size: 1rem;
    border-radius: 5px;
    border: 1px solid #ddd;
    transition: border-color 0.3s ease;
}

.language-selector select:hover,
.language-selector select:focus {
    border-color: #007bff;
}
